.header {
  background-color: #fff;
  justify-content: flex-end;
  display: flex;
  position: relative;
}

.header__logo {
  position: absolute;
  top: 10px;
  left: 50px;
}

.nav {
  text-transform: uppercase;
  align-items: center;
  margin-right: 50px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  display: flex;
}

.nav__link {
  color: #060b35;
  display: inline-block;
}

.nav__link:not(:first-child) {
  margin-left: 20px;
}

.nav__link:hover {
  color: #00acdc;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition-duration: .2s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__image-container {
  justify-content: center;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
}

.card__title {
  color: #060b35;
  padding-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover .card__title {
  color: #34568b;
}

.card__code {
  color: #616070;
  padding-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__stars-container {
  display: flex;
}

.card__stars {
  justify-content: space-between;
  padding-top: 16px;
  display: flex;
}

.card__stars-star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.card__stars-star:last-of-type {
  background-image: url("star.822d561e.svg");
}

.card__stars-star:not(:last-of-type) {
  margin-right: 4px;
}

.card__stars-reviews {
  color: #060b35;
  align-self: flex-end;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  padding-top: 24px;
  padding-bottom: 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-number {
  color: #060b35;
  font-size: 16px;
  font-weight: 600;
  line-height: 18px;
}

.card__button-container {
  box-sizing: border-box;
  justify-content: center;
  height: 40px;
  display: flex;
}

.card__button {
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
}

.card__button--styles {
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #00acdc;
  font-size: 14px;
  font-weight: 600;
  line-height: 16px;
  text-decoration: none;
}

.card__button--styles:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: Roboto, serif;
}

.container {
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 308px) {
  .container {
    grid-template-columns: repeat(1, 200px);
  }
}

@media (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
